import { Pipe, PipeTransform } from '@angular/core';

/**
 * Gets decimals count
 * {{ 12.4 | bbGetDecimalsCount }}
 */
@Pipe({ name: 'bbGetDecimalsCount' })
export class GetDecimalsCountPipe implements PipeTransform {
  transform(value: number | string | undefined): number {
    return value?.toString().split('.')[1]?.length ?? 0;
  }
}
